"use client"
import Hero from '../components/Hero'
import WhatWeDo from '../components/WhatWeDo'
import ProjectCard from '../components/ProjectCard'
import SectionHeader from '../components/SectionHeader'
import PartnerCTA from '../components/PartnerCTA'
import Link from 'next/link'
import { projects, Project } from '../data/projects'
import { useEffect, useState } from 'react'

export default function Home() {
  const [randomSystems, setRandomSystems] = useState<Project[]>([])

  useEffect(() => {
    // Shuffle and pick 3 projects
    const shuffled = [...projects].sort(() => 0.5 - Math.random())
    setRandomSystems(shuffled.slice(0, 3))
  }, [])

  return (
    <>
      <Hero />

      <WhatWeDo />

      {/* Featured Systems Section */}
      <section className="py-24 px-6 border-t border-white/5">
        <div className="max-w-7xl mx-auto">
          <div className="flex justify-between items-end mb-12">
            <SectionHeader
              label="Selected Work"
              title="Featured Experiments"
              description="Top-tier systems engineering cases that demonstrate high-leverage outcomes across different categories."
            />
            <Link href="/systems" className="hidden md:block text-xs font-bold uppercase tracking-widest text-white/40 hover:text-white transition-colors mb-4 border-b border-white/10 pb-1">
              View All Systems
            </Link>
          </div>

          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-1">
            {randomSystems.length > 0 ? (
              randomSystems.map((project) => (
                <ProjectCard key={project.slug} project={project} />
              ))
            ) : (
              // Initial skeleton-like state or featured fallback for hydration
              projects.slice(0, 3).map((project) => (
                <div key={project.slug} className="opacity-0 animate-pulse">
                  <ProjectCard project={project} />
                </div>
              ))
            )}
          </div>
        </div>
      </section>

      {/* Strategy Teaser */}
      <section className="py-24 px-6 border-t border-white/5 bg-gradient-to-b from-black to-[#0d0d0d]">
        <div className="max-w-7xl mx-auto text-center space-y-12">
          <SectionHeader
            label="Doctrine"
            title="Systems Over Slogans"
          />
          <div className="grid md:grid-cols-3 gap-12 text-left">
            <div className="space-y-4">
              <h3 className="font-bold uppercase tracking-tighter text-2xl">Decision Velocity</h3>
              <p className="text-white/40 text-sm leading-relaxed">We prioritize systems that reduce the time between signal detection and outcome execution. Speed is a primary strategic metric.</p>
            </div>
            <div className="space-y-4">
              <h3 className="font-bold uppercase tracking-tighter text-2xl">Signal Clarity</h3>
              <p className="text-white/40 text-sm leading-relaxed">The goal is to eliminate operational noise. Our tools surface the critical information required for power concentration.</p>
            </div>
            <div className="space-y-4">
              <h3 className="font-bold uppercase tracking-tighter text-2xl">Coordinate Power</h3>
              <p className="text-white/40 text-sm leading-relaxed">Systems win when they align human intent with machine precision. We build the interfaces that enable this trust.</p>
            </div>
          </div>
          <div className="pt-8">
            <Link href="/strategy" className="inline-block px-8 py-4 border border-white/20 hover:bg-white/5 transition-colors uppercase text-xs tracking-widest font-bold">
              Read our Strategy Doctrine
            </Link>
          </div>
        </div>
      </section>

      <PartnerCTA />
    </>
  )
}
